


// card__list
$text-dark:rgb(35, 34, 34);
$text-secondary:gray;
.card{
    border:  1px solid lightgray;
    border-radius: 5px;

    border: {
        radius:5px;
        width: 5px;
        style:solid;
        color: lightgray; 
    }
    .card__body{
        padding: 15px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        figure{
            padding: 0;
            margin: 0;
            img{
                border-radius: 5px;
                width: 136px;
                height: 136px;
            }
        }
    }
    .card__details{
        display: flex;
        flex-direction: column;
        .card__title{
            flex-grow: 1;
            font-size: 18px;
            font-weight: 600;
            color: $text-dark;
        }
        .card__price{
            color: $text-secondary;
        }
        .card__author{
            color: $text-secondary;
        }
    }
}
.card__list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
} 

